@import url(https://fonts.googleapis.com/css2?family=Fira+Mono:wght@400;500;700&display=swap);
/* Define the global default variables up front. */
:root {
  --html-bg: hsl(220, 50%, 5%);
  
  font-family: 'Fira Mono', monospace;
}

:root, .App--theme-dark {
  --bg: hsl(220, 10%, 15%);
  --text-primary: hsl(220, 20%, 90%);
  --text-secondary: hsl(220, 10%, 60%);
  --icon: hsl(220, 10%, 100%);

  --widget-inset: hsl(220, 15%, 80%);
  
  --container-radius: 8px;
  --widget-radius: 4px;

  --widget-inactive-text: hsl(220, 20%, 20%);
  --widget-active-text: hsl(220, 20%, 0%);

  --widget-highlight: hsl(220, 0%, 100%);
  
  --widget-inactive: hsl(220, 10%, 50%);
  --widget-active: hsl(220, 0%, 15%);
  
  --widget-inactive-highlight: var(--widget-inset);
  --widget-active-highlight: hsl(220, 0%, 100%);
  
  --widget-inactive-shadow: hsla(220, 0%, 0%, 0.1);
  --widget-active-shadow: hsla(220, 0%, 0%, 0.5);
  
  --widget-inactive-border: hsl(220, 10%, 70%);
  --widget-active-border: hsl(220, 10%, 40%);
  
  --widget-inactive-glow: hsla(220, 0%, 100%, 0.7);
  --widget-active-glow: hsla(220, 0%, 100%, 0.9);
}

.App {
  height: 100%;
  width: 100%;

  background: hsl(220, 50%, 5%);

  background: var(--html-bg);
  color: var(--text-primary);

  font-size: 12px;
}

.App--theme-light {
  --bg: hsl(220, 10%, 95%);
  --text-primary: hsl(220, 20%, 10%);
  --text-secondary: hsl(220, 10%, 60%);
  --icon: hsl(220, 10%, 0%);

  --widget-inset: hsl(220, 15%, 80%);
  
  --container-radius: 8px;
  --widget-radius: 4px;

  --widget-inactive-text: hsl(220, 20%, 20%);
  --widget-active-text: hsl(220, 20%, 0%);

  --widget-highlight: hsl(220, 0%, 100%);
  
  --widget-inactive: hsl(220, 10%, 50%);
  --widget-active: hsl(220, 0%, 15%);
  
  --widget-inactive-highlight: var(--widget-inset);
  --widget-active-highlight: hsl(220, 0%, 100%);
  
  --widget-inactive-shadow: hsla(220, 0%, 0%, 0.1);
  --widget-active-shadow: hsla(220, 0%, 0%, 0.5);
  
  --widget-inactive-border: hsl(220, 10%, 70%);
  --widget-active-border: hsl(220, 10%, 40%);
  
  --widget-inactive-glow: hsla(220, 0%, 100%, 0.7);
  --widget-active-glow: hsla(220, 0%, 100%, 0.9);
}

.OrreryViewer {
  width: 100%;
  height: 100%;
}


.PageHeader {
  background-color: var(--bg);
  color: var(--text-primary);
  width: 256px;
  height: 48px;

  position: absolute;
  left: 16px;
  top: 16px;
  border-radius: var(--container-radius);

  padding: 0 16px;

  z-index: 32;

  display: flex;
  align-items: center;
  justify-content: flex-start;

  font-weight: bold;
}


.EmptyState {
  display: flex;
  
  align-items: center;
  justify-content: center;

  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.EmptyState .box {
  display: flex;
  flex-direction: column;
  
  align-items: center;
  justify-content: center;

  padding: 16px;

  background-color: var(--bg);
  border-radius: var(--container-radius);
}

.EmptyState .box > * + * {
  margin-top: 8px;
}

.EmptyState .box h1 {
  font-size: 1.33em;
  font-weight: bold;
}

.EmptyState.App--theme-dark .box {
  background-color: transparent;
  color: var(--text-primary);
}

.EmptyState.App--theme-dark .box h2 {
  color: var(--text-secondary);
}


.Switch {
  display: flex;

  align-items: center;
  justify-content: center;
  height: 32px;
  min-width: 32px;
}

.Switch__label {
  flex: 1 1;
  font-weight: 500;
  min-width: 96px;

  color: var(--widget-inactive-text);

  -webkit-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.Switch__element {
  position: relative;
  display: block;
  
  border-radius: var(--widget-radius);
  width: 32px;
  height: 16px;
  background-color: var(--widget-inset);
  box-shadow: inset 0 0 0 2px var(--widget-inactive-border), inset 0 2px 8px var(--widget-inactive-shadow);
  
  transition: transform 0.1s ease-out, box-shadow 0.05s ease-out;
}

.Switch__element::after {
  position: absolute;
  display: block;
  content: '';
  
  background-color: var(--widget-inactive-highlight);
  border-radius: var(--widget-radius);
  width: 16px;
  height: 24px;
  box-shadow: inset 0 0 0 7px var(--widget-inactive), 0 0 6px 1px var(--widget-inactive-glow);
  left: 0;
  top: -4px;

  transition: transform 0.1s ease-out, background-color 0.05s ease-out;
}

.Switch--checked .Switch__element {
  box-shadow: inset 0 0 0 2px var(--widget-active-border), inset 0 2px 8px var(--widget-active-shadow);
}

.Switch--checked .Switch__label {
  color: var(--widget-active-text);
}

.Switch--checked .Switch__element::after {
  transform: translateX(16px);
  
  background-color: var(--widget-active-highlight);
  box-shadow: inset 0 0 0 7px var(--widget-active), 0 0 8px 2px var(--widget-active-glow);
}


@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.CircularProgress {
  transition: opacity 0.35s ease;
}

.CircularProgress--hidden {
  opacity: 0;
}

.CircularProgress--spinning {
  display: inline-block;
}

.CircularProgress--spinning svg {
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.CircularProgress svg g path {
  stroke: var(--icon) !important;
}


.OrreryViewer {
  background-color: var(--html-bg);
}

.OrreryViewer canvas {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;

  opacity: 0;

  transition: opacity 0.5s ease-out;
}
  
.OrreryViewer--loaded canvas {
  opacity: 1;
}

.OrreryViewer__loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  transition: opacity 0.5s ease-out;
}

.OrreryViewer--loaded .OrreryViewer__loading {
  opacity: 0;
  pointer-events: none;
}

.OrreryViewer__options {
  background-color: var(--bg);
  color: var(--text-primary);

  border-radius: var(--container-radius);

  position: absolute;

  top: 16px;
  right: 16px;

  display: flex;
  flex-direction: column;
  padding: 8px 16px;
  min-width: 256px;
}

.OrreryViewer__options .stats {
  border-top: 2px dashed var(--widget-inactive-border);
  margin-top: 8px;
  padding-top: 8px;
  display: block;
}

.OrreryViewer__options .stats li {
  list-style-type: none;
  height: 32px;
  line-height: 32px;
  -webkit-user-select: contain;
      -ms-user-select: element;
          user-select: contain;
}

.OrreryViewer__progress {
  display: flex;
}

.OrreryViewer__progress span {
  flex: 1 1;
}

.OrreryViewer__info {
  position: absolute;
  bottom: 8px;
  right: 8px;
}

